import React from 'react';

import projectData from './../../data/projects.json';
import Project from './Project';

const ProjectList = ( { categories, language, data } ) => {

  const projects = (language == "eng" ? projectData.projectsENG : projectData.projectsPL);
  return (
    <div className='project-list'>
        {
            projects.map((project) => {
                if(categories.length > 0) {
                    const multipleExist = categories.every((c) => {
                        return project.category.includes(c);
                    });
                    if(multipleExist) {
                        return <Project key={project.name} project={project} modalData={data.modal} />
                    }
                } else {
                    return <Project key={project.name} project={project} modalData={data.modal} />
                }
            })
        }
        <span className='no-data'>{data.nodata}</span>
    </div>
  );
}

export default ProjectList;