import React, { useRef, useState } from 'react';
import emailjs from '@emailjs/browser';
import { motion } from 'framer-motion';
import { sendVariants } from './animations'

import './../styles/Contact.css';

const Contact = ( { data } ) => {
  const formRef = useRef();
  const nameRef = useRef();
  const emailRef = useRef();
  const topicRef = useRef();
  const messageRef = useRef();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [info, setInfo] = useState(false);
  const [nameError, setNameError] = useState(false);
  const [emailError, setEmailError] = useState(false);
  const [topicError, setTopicError] = useState(false);
  const [messageError, setMessageError] = useState(false);
  const [sendHover, setSendHover] = useState(false);

  const handleMouseEnter = () => {
    setSendHover(true);
  }

  const handleMouseLeave = () => {
    setSendHover(false);
  }
  
  const handleSubmit = (e) => {
    e.preventDefault();

    let errorMessage = "";
    setError("");
    setNameError(false);
    setEmailError(false);
    setTopicError(false);
    setMessageError(false);

    if (nameRef.current.value.length < 5) {
      setNameError(true);
      errorMessage += data.val_name + "\n";
    }

    let emailValidation = /\S+@\S+\.\S+/;

    if(!emailValidation.test(emailRef.current.value)) {
        setEmailError(true);
        errorMessage += data.val_email + "\n";
    }

    if (topicRef.current.value.length < 8) {
      setTopicError(true);
      errorMessage += data.val_topic + "\n";
    }

    if (messageRef.current.value.length < 8) {
      setMessageError(true);
      errorMessage += data.val_message + "\n";
    }

    if (errorMessage !== "") {
      return setError(errorMessage);
    }

    sendEmail();
  }

  const sendEmail = () => {
    setLoading(true);
    try {
      emailjs.sendForm(process.env.REACT_APP_EMAIL_SERVICE_ID, process.env.REACT_APP_EMAIL_TEMPLATE_ID, formRef.current, process.env.REACT_APP_EMAIL_PUBLIC_KEY)
        .then((result) => {
            setInfo(data.send_info);
            nameRef.current.value = "";
            emailRef.current.value = "";
            topicRef.current.value = "";
            messageRef.current.value = "";
        }, (error) => {
            setError(data.send_error);

        });
        setTimeout(() => {
          setInfo(false);
        }, 5000);
    } catch {
      setError(data.send_error);
    }
    setTimeout(() => {
      setLoading(false);
    }, 2000);
  }

  return (
    <section id='contact'>
      <h2>{data.title}</h2>
        <form ref={formRef} onSubmit={handleSubmit}>
          <input type='text' name='user_name' ref={nameRef} placeholder={data.name} className={nameError ? 'error-input' : ''} required />
          <input type='email' name='user_email' ref={emailRef} placeholder={data.email} className={emailError ? 'error-input' : ''} required />
          <input type='text' name='user_topic' ref={topicRef} placeholder={data.topic} className={topicError ? 'error-input' : ''} required />
          <textarea name='message' ref={messageRef} placeholder={data.message} className={messageError ? 'error-input' : ''} required></textarea>
          <p className='error'>{error}</p>
          <div className='send-box'>
            <button type='submit' className={loading ? 'loading' : ''} onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
              {data.send}
              <motion.span
                variants={sendVariants}
                animate={sendHover ? 'hover' : 'initial'}
                transition={{duration: 0.3}}
                className='material-symbols-outlined'
                >
                send
              </motion.span>
            </button>
            {loading && <div className="lds-ellipsis"><div></div><div></div><div></div><div></div></div>}
          </div>
        </form>
        {info && <div className='info-box'>
          <span>Twoja wiadomość została wysłana</span>
        </div>}
    </section>
  );
}

export default Contact;