import React, { useState } from 'react';

import data from './../../data/categories.json';
import Overlay from './Overlay';

const Project = ( { project, modalData } ) => {
  const [open, setOpen] = useState(false);
  const icons = data.categories;

  const openProject = () => {
    document.body.classList.add('modal-open');
    setOpen(true);
  }

  const closeProject = () => {
    document.body.classList.remove('modal-open');
    setOpen(false);
  }

  return (
    <>
        <div className='project' onClick={openProject}>
            <div className='preview'>
                <img
                    src={project.gallery[0]}
                    alt={project.name + ' preview'}
                />
            </div>
            <div className='info'>
                <span className='name'>{project.name}</span>
                <div className='technologies'>
                    {
                        project.category.map((c) => {
                            return (
                                <div key={project.name + c}>
                                    <img
                                        src={icons.filter(ct => ct.name === c)[0].icon}
                                        alt={c + ' icon'}
                                    />
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        </div>
        {open && <Overlay close={closeProject} project={project} modalData={modalData} icons={icons} />}
    </>
  );
}

export default Project;