import React from 'react';
import Modal from './Modal';

import './../../styles/Overlay.css';

const Overlay = ( { project, close, modalData, icons } ) => {

  const closeModal = (event) => {
    if (event.target.id === "modalBcg") {
      event.preventDefault();
      close();
    }
  };

  return (
    <div className='overlay'>
        <div className='overlay__background' id='modalBcg' onClick={closeModal}>
            <Modal project={project} close={close} modalData={modalData} icons={icons} />
        </div>
    </div>
  );
}

export default Overlay;