import React, { useState } from 'react';

const Category = ( { categories, setCategories, category } ) => {
  const [choosen, setChoosen] = useState(false);
  const handleClick = () => {
    if(!choosen) {
      setCategories(prevCategories => [...prevCategories, category.name]);
    } else {
      setCategories(prevCategories => [...prevCategories].filter(c => c !== category.name));
    }
    setChoosen(!choosen);
  }

  return (
    <div className={choosen ? 'category selected' : 'category'} onClick={handleClick}>
        <span>{category.name}</span>
        <img
          src={category.icon}
          alt={category.name + ' icon'}
        />
    </div>
  )
}

export default Category;