import React from 'react';
import { motion } from 'framer-motion';
import { logoPathVariants, switchSpring } from './animations'
import './../styles/Header.css';

const Header = ( {theme, toggleTheme, language, toggleLanguage, data} ) => {

  return (
    <section id='header'>
        <div className='header-box'>
            <svg
                className='logo' xmlns="http://www.w3.org/2000/svg" viewBox="0 0 443 254" strokeWidth="30">
                <motion.path
                    variants={logoPathVariants}
                    initial='hidden'
                    animate='visible'
                    d="M0,1525L0,510h235h235l-2,815c-2,448-1,815,2,815s94-366,201-812L866,515l229-3l230-2l194,817c107,450,198,822,203,828c4,5,8-363,8-818v-827h235h235v1015v1015h-384-383l-6-27c-13-64-318-1400-324-1418-6-20-6-20-13,0-4,11-81,341-171,733L755,2540h-377-378v-1015Z"
                    transform="matrix(.083841 0 0-.083841 37.0498 233.394228)"
                />
                <motion.path
                    variants={logoPathVariants}
                    initial='hidden'
                    animate='visible'
                    d="M2360,1526v-1016h235h235l2,803l3,802l200-1c147,0,224-5,289-18c256-50,426-165,525-355c74-143,103-280,102-491c0-407-164-673-476-775-170-55-145-55-1877-55L0,420v-210L0,0h1610C2680,0,3253,4,3318,11c342,37,586,143,783,338c267,267,379,708,303,1201-50,326-188,572-419,743-99,74-187,118-323,164-210,70-258,75-809,80l-493,5v-1016Z"
                    transform="matrix(.083841 0 0-.083841 37.0498 233.561911)"
                />
            </svg>
            <div className='menu'>
                <a href='#about'>{data.about}</a>
                <a href='#projects'>{data.projects}</a>
                <a href='#contact'>{data.contact}</a>
                <div className='settings'>
                    <span className='material-symbols-outlined'>settings</span>
                    <div className="dropdown-content">
                        <span className='category'>{data.theme}</span>
                        <div className='switch-row'>
                            <span className='material-symbols-outlined'>dark_mode</span>
                            <div className='switch' data-ison={theme === 'light' ? 'true' : 'false'} onClick={toggleTheme}>
                                <motion.div className="handle" layout transition={switchSpring} />
                            </div>
                            <span className='material-symbols-outlined'>light_mode</span>
                        </div>
                        <span className='category'>{data.language}</span>
                        <div className='switch-row'>
                            <svg className='flag' xmlns="http://www.w3.org/2000/svg" viewBox="0 0 640 480">
                                <path fill="#012169" d="M0 0h640v480H0z"/>
                                <path fill="#FFF" d="m75 0 244 181L562 0h78v62L400 241l240 178v61h-80L320 301 81 480H0v-60l239-178L0 64V0h75z"/>
                                <path fill="#C8102E" d="m424 281 216 159v40L369 281h55zm-184 20 6 35L54 480H0l240-179zM640 0v3L391 191l2-44L590 0h50zM0 0l239 176h-60L0 42V0z"/>
                                <path fill="#FFF" d="M241 0v480h160V0H241zM0 160v160h640V160H0z"/>
                                <path fill="#C8102E" d="M0 193v96h640v-96H0zM273 0v480h96V0h-96z"/>
                            </svg>
                            <div className='switch' data-ison={language === 'pl' ? 'true' : 'false'} onClick={toggleLanguage}>
                                <motion.div className="handle" layout transition={switchSpring} />
                            </div>
                            <svg className='flag' xmlns="http://www.w3.org/2000/svg" viewBox="0 0 640 480">
                                <g fillRule="evenodd">
                                    <path fill="#fff" d="M640 480H0V0h640z"/>
                                    <path fill="#dc143c" d="M640 480H0V240h640z"/>
                                </g>
                            </svg>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
  );
}

export default Header;