import React, { useState, useEffect } from 'react';

import Header from './components/Header';
import Baner from './components/Baner';
import About from './components/About';
import Projects from './components/Projects';
import Contact from './components/Contact';
import Footer from './components/Footer';

import pl from './data/pl.json';
import eng from './data/eng.json';

import './App.css';

const LOCAL_STORAGE_KEY_THEME = 'portfolio.theme';
const LOCAL_STORAGE_KEY_LANGUAGE = 'portfolio.language';

const App = () => {
  const [theme, setTheme] = useState('dark');
  const [language, setLanguage] = useState('eng');
  const [data, setData] = useState(eng);

  useEffect(() => {
    if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
      setTheme('dark');
    } else {
      setTheme('light');
    }

    const storedTheme = localStorage.getItem(LOCAL_STORAGE_KEY_THEME);
    if (storedTheme) setTheme(storedTheme);
    const storedLanguage = localStorage.getItem(LOCAL_STORAGE_KEY_LANGUAGE);
    if (storedLanguage) {
      setLanguage(storedLanguage);
      setData(storedLanguage === 'eng' ? eng : pl);
    }
  }, []);

  const toggleTheme = () => {
    localStorage.setItem(LOCAL_STORAGE_KEY_THEME, (theme === 'dark' ? 'light' : 'dark'));
    setTheme((current) => (current === 'dark' ? 'light' : 'dark'));
  }

  const toggleLanguage = () => {
    localStorage.setItem(LOCAL_STORAGE_KEY_LANGUAGE, (language === 'eng' ? 'pl' : 'eng'));
    setLanguage((current) => (current === 'eng' ? 'pl' : 'eng'));
    setData((current) => (current === eng ? pl : eng));
  }

  return (
    <div id={theme}>
        <Header theme={theme} toggleTheme={toggleTheme} language={language} toggleLanguage={toggleLanguage} data={data.header} />
        <Baner data={data.baner} theme={theme} />
        <About data={data.about} />
        <Projects data={data.projects} language={language} />
        <Contact data={data.contact} />
        <Footer data={data.footer} />
    </div>
  );
}

export default App;