import React from 'react';
import uuid from 'react-uuid';

import categoriesData from './../data/categories.json';
import toolsData from './../data/tools.json';

import './../styles/About.css';

const About = ( { data } ) => {
  const technologies = categoriesData.categories.filter(c => c.about === true);
  
  return (
    <section id='about'>
      <h2>{data.title}</h2>
      <p>{data.description}</p>
      <div className='about-box'>
        <div className='card'>
          <span className='material-symbols-outlined'>emoji_people</span>
          <h3>{data.strengths}</h3>
          <ul>
            {
              data.strengthslist.map((s) => {
                return <li key={s}>{s}</li>
              })
            }
          </ul>
        </div>
        <div className='card'>
          <span className='material-symbols-outlined'>terminal</span>
          <h3>{data.technologies}</h3>
          <div className='technologies'>
            {
              technologies.map(c => {
                return <div className='item' key={uuid()}>
                        <div className='icon'>
                          <img src={c.icon} alt={c.name + ' icon'} />
                        </div>
                        <span>{c.name}</span>
                       </div>
              })
            }
          </div>
        </div>
        <div className='card'>
          <span className='material-symbols-outlined'>construction</span>
          <h3>{data.tools}</h3>
          <div className='list'>
            {
              toolsData.tools.map(tool => {
                return <div className='item' key={uuid()}>
                        <div className='icon'>
                          <img src={tool.icon} alt={tool.name + ' icon'} />
                        </div>
                        <span>{tool.name}</span>
                       </div>
              })
            }
          </div>
        </div>
      </div>
    </section>
  );
}

export default About;